.poster {
  width: 300px;
}

.movieWrap {
  display: flex;
  gap: 50px;
}

.movieInfo {
  max-width: 800px;
}

.title {
  display: block;
  color: #d82923;
  margin: 0;

}
.title:not(:first-child) {
  margin-top: 20px;
}

.text {
  color: #fff;
  margin: 0;
  margin-top: 10px;
}
.textOverview {
  color: #fff;
  margin: 0;
  font-size: 18px;
  line-height: calc(28 / 18);
  overflow: auto;
  max-height: 205px;
  margin-top: 10px;
}
.textOverview::-webkit-scrollbar {
  width: 4px;
}

.textSpan {
  color: #d82923;

  font-size: 20px;
  font-weight: 600;
}

.list {
  margin-top: 16px;
  display: flex;
}

.item {
  color: #fff;
}

.item:not(:last-child)::after {
  content: ',';
  margin-right: 10px;
}
.imgWrap {
  position: relative;
}
.nav {
  position: absolute;
  bottom: -30px;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #b72f2b;
}

.link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  font-size: 18px;
  width: 150px;
  text-decoration: none;
  color: #fff;
  transition: background-color 100ms linear;
}

.link:hover,
.link:focus,
.active {
  background-color: #d82923;
}

